export class Bool {

    private data: any = {}

    public static make(): Bool {
        return new Bool();
    }

    public must(value: any): this {
        return this.add('must', value);
    }

    public filter(value: any): this {
        return this.add('filter', value);
    }

    public should(value: any): this {
        return this.add('should', value);
    }

    public mustNot(value: any): this {
        return this.add('must_not', value);
    }

    public minimumShouldMatch(value: number): this {
        return this.set('minimum_should_match', value);
    }

    public boost(value: number = 1): this {
        return this.set('boost', value);
    }

    private set(key: string, value: any): this {
        this.data[key] = value;
        return this;
    }

    private add(type: string, value: any): this {
        if (!this.data[type]) this.data[type] = [];
        this.data[type].push(value);
        return this;
    }

    public toJSON() {
        return { bool: this.data };
    }
}